body,
html,
#root {
  height: 100%;
}

html {
  font-size: 18px;
}

h1 {
  padding: .77rem 0;
  background-color: #0b25bb;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 1.88rem;
  color: #fff;
  margin-top: 0;
  margin-bottom: 0;
}

h1 .highlight {
  color: #3fb4e2;
}

.App {
  height: 100%;
  padding: 0 10% 10% 10%;
  background-image: url('./background_photo_desktop.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App {
    padding: 0 10% 10% 10%;
  }
  .App-playlist {
    align-items: center;
    flex-direction: column;
    
  }
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #0b25bb;
  margin-top: 0;
  margin-bottom: 0;
  padding: 1.5rem 0;
  height: 3rem;
}

.footer a {
  padding: 1rem;
}

.footer p {
  color: white;
  font-size: 1.5rem;
  font-weight: 500;
}

.social {
  color: white;
}